.comment-section {
  padding-top: 40px;
  width: 100%;
}

.comment-input {
  display: flex;
  flex-direction: column;
}

textarea {
  font-size: 11pt;
  border: 1px solid #1f74d0;
  padding: 10px;
  outline: none;
  resize: none;
  margin-bottom: 12px;
}

.flex-right {
  display: flex;
  flex-direction: row-reverse;}

.btn {
  font-size: 12px;
  border-radius: 10px;
  cursor: pointer;
  border: none;
  outline: none;
  box-shadow: 1px 1px 5px 0px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 3px 1px -2px rgba(0,0,0,0.12);
}

.send {
  background-color: #1f74d0;
  color: #fff;
  padding: 15px 25px;
}

.send:hover {
  background-color: #154e8b;
}

.comment-box {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: flex-end;
  background: #fff;
  padding: 0px 20px;
  font-size: 13px;
  margin-bottom: 10px;
}

.comment-text {
  margin-right: 10px;
}

.delete {
  background-color: #fb4144;
  color: #fff;
  padding: 10px 15px;
  font-size: 10px !important;
}

.delete:hover {
  background-color: #bd2f32;
}

.edit {
  background-color: #808082;
  color: #fff;
  padding: 10px 15px;
  margin-right: 10px;
  font-size: 10px !important;
}

.edit:hover {
  background-color: #616161;
}